<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input v-model="queryInfo.query" placeholder="" prefix-icon="el-icon-search"
            @keyup.enter.native="queryOrdersList" clearable @clear="queryOrdersList">
            <el-button @click="queryOrdersList" slot="append"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border>
        <el-table-column label="" type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="order_pay">
          <template slot-scope="scope">
            <el-tag :type="scope.row.order_pay == '1'? 'success': 'warning'">
              {{scope.row.order_pay == '0'?'':''}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{scope.row.create_time |filterTime}}
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right" width="150px">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-edit" @click="editOrder(scope.row)"></el-button>
            <el-button type="text" icon="el-icon-box" @click="showKuaidi(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum" :page-sizes="[ 5, 10,20,50,100]" :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible="addressDialogVisible" @close="addressDialogClosed">
      <el-form :model="address" :rules="addressRuls" ref="addressRef">
        <el-form-item label="/" prop="address1">
          <el-cascader v-model="address.address1" :options="cityData" @change="handleChange"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="address.address2" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="subAddress()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible="kuaidiDialogVisible" @close="kuaidiDialogClosed">
      <!-- <el-form :model="kuaidi" ref="kuaidiRef"> -->
      <el-timeline :reverse="false">
        <el-timeline-item v-for="(activity, index) in kuaidi" :key="index" :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
      <!-- </el-form> -->
      <span slot="footer">
        <el-button @click="kuaidiDialogVisible = false"> </el-button>
        <el-button type="primary" @click="kuaidiDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5,
        user_id: ''
      },
      ordersList: [],
      total: 0,
      addressDialogVisible: false,
      address: {
        address1: [],
        address2: ''
      },
      addressRuls: {
        address1: [{ required: true, message: '', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      cityData,
      kuaidiDialogVisible: false,
      kuaidi: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      console.log('res', res)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    // 
    queryOrdersList() {
      console.log('queryOrdersList', this.queryInfo.query)
      this.getOrdersList()
    },
    // 
    handleSizeChange(newSize) {
      // console.log('newSize', newSize)
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    handleCurrentChange(newPage) {
      // console.log('newPage', newPage)
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    // 
    editOrder(row) {
      this.addressDialogVisible = true
    },
    handleChange(value) {
      console.log(value)
    },
    subAddress() {
      console.log('subAddress', this.address)
    },
    addressDialogClosed(done) {
      this.addressDialogVisible = false
      this.$refs.addressRef.resetFields()
      console.log('done', done)
    },
    async showKuaidi(row) {
      //  id
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      // const { data: res } = await this.$http.get(`/kuaidi/${row.order_id}`)
      if (res.meta.status !== 200) {
        this.$message.error(res.meta.msg)
      }
      console.log('res', res)
      this.kuaidi = res.data
      this.kuaidiDialogVisible = true
    },
    kuaidiDialogClosed(done) {
      this.kuaidiDialogVisible = false
      // this.$refs.kuaidiRef.resetFields()
      console.log('done', done)
    }
  }
}
</script>
